.library{
    position: fixed;
    top: 0;
    left: 0;
    width: 20rem;
    height: 100%;
    background: rgb(255, 255, 255);
    overflow: scroll;
    transform: translateX(-100%);
    transition: all 0.5s ease;
    opacity: 0;
    h2{
        padding: 2rem;
    }
}

.library-song{
    display: flex;
    // flex-direction: column;
    align-items: center;
    padding: 1rem 2rem 1rem 2rem;
    cursor: pointer;
    img{
        width: 20%;
    }
    &:hover{
        background: rgb(208, 216, 238);
    }
}

.song-description{
    padding-left: 1rem;
    h3{
        font-size: 1rem;
    }
    h4{
        font-size: 0.7rem;
    }
}

/* Works on Chrome/Edge/Safari */
*::-webkit-scrollbar {
    width: 5px;
  }
  *::-webkit-scrollbar-track {
    background: transparent;
  }
  *::-webkit-scrollbar-thumb {
    background-color: rgba(155, 155, 155, 0.7);
    border-radius: 20px;
    border: transparent;
  }

  .selected{
      background: rgb(247, 198, 247);
  }

  .active-library{
      transform: translateX(0);
      opacity: 1;

  }
  @media screen and (max-width: 768px) {
    .library {
      width: 100%;
    }
  }
  